<template>
  <div>
    <!---->
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth"  >
              <el-select  v-model="imageName" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="foodAttributeTypeList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="propertyID">
        </el-table-column>

        <el-table-column
          align="center"
          label=""
          prop="propertyName">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="parentName">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="letterName">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row)">
            </el-button>
            <!--<el-button-->
              <!--v-show="true"-->
              <!--size="mini"-->
              <!--type="danger"-->
              <!--@click="Delete(scope.row.propertyID)">-->
            <!--</el-button>-->
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.fd_py_Name" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.data.fd_py_ParentID" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateFoodAttributeTypeOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateFoodAttributeTypeOptions.data.fd_py_ID" :disabled="isOff" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateFoodAttributeTypeOptions.data.fd_py_Name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateFoodAttributeTypeOptions.data.fd_py_ParentID" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    data(){
      return {
        updateFoodAttributeTypeOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_py_ID": "",//
            "fd_py_Name": "",//
            "fd_py_ParentID": "",//
          }
        },
        options:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'28'
          },
        ],
        //
        imageName:'',
        isOff:'',
        total:0,
        isLoading:false,
        updateDialog:false,
        formLabelWidth:'120px',
        addDialog:false,
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_py_Name": "",//
            "fd_py_ParentID": "",//
          },
          fd_py_ParentID:'',
        },
      }
    },
    computed: mapGetters([
      'foodAttributeTypeList',
    ]),
    created(){
      this.initData('',1)
    },
    methods: {
      //

      handleCurrentChange(num){
        this.initData(this.imageName,num)
      },

      initData(name,page){
        let options = {
            "loginUserID": "huileyou",
            "loginUserPass": "123",
            "operateUserID": "",
            "operateUserName": "",
            "pcName": "",
            //"fd_py_ID": "28",//
            //"fd_py_Name": "",//
            "fd_py_ParentID": name?name:'',//
            //"fd_py_LetterName": "C",//
            "page": page?page:"1",
            "rows": "5",

          };
        this.isLoading = true;
        this.$store.dispatch('initFoodAttributeListType',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search(){
        this.initData(this.imageName,1)
   },


      //
      Add(){
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addSubmit(){
        //actions
        this.$store.dispatch('AddFoodAttributeListType',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.imageName,1) //
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.addDialog = false;
      },
      //
      Update(rowData){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.updateFoodAttributeTypeOptions.data.fd_py_ParentID="";
        this.updateFoodAttributeTypeOptions.data.fd_py_ID=rowData.propertyID;
        this.updateFoodAttributeTypeOptions.data.fd_py_Name=rowData.propertyName;
        // this.updateFoodAttributeTypeOptions.data.fd_py_ParentID=rowData.propertyID;
        // this.$store.commit('initUpdateFoodAttributeType',id)   //initUpdataFoodRecommendStoreTypemutations
      },
      //
      updateSubmit(){
        // this.updateFoodAttributeTypeOptions.data.fd_py_ParentID=;
        this.$store.dispatch('initUpdateFoodAttributeType',this.updateFoodAttributeTypeOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.imageName,1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false;
      },
      //
//       Delete(id){
//         let deleteOptions = {
//           "loginUserID": "huileyou",
//           "loginUserPass": "123",
//           "operateUserID": "",
//           "operateUserName": "",
//           "pcName": "",
//           "data": {
//             "fd_py_ID": id
//           }
//         };
// //        DeleteFoodAttributeType
//         this.$store.dispatch('DeleteFoodAttributeType',deleteOptions)
//           .then((suc)=>{
//             this.$notify({
//               message: suc,
//               type: 'success'
//             });
//             this.initData(this.imageName,1)
//           },err=>{
//             this.$notify({
//               message: err,
//               type: 'error'
//             });
//           });
//       }
    }
  }
</script>
<style scoped>

</style>



