<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="adTypeName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table
        :data="adTypeList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          sortable
          label=""
          prop="sm_at_ID">
        </el-table-column>
        <el-table-column
          align="center"
          sortable
          label=""
          prop="sm_at_Name">
        </el-table-column>
        <el-table-column
          align="center"
          sortable
          label=""
          prop="sm_at_Cost">
        </el-table-column>
        <el-table-column
          align="center"
          sortable
          label=""
          prop="sm_at_CreateTime">
        </el-table-column>
        <el-table-column
          align="center"
          sortable
          label=""
          prop="sm_at_Remark">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.sm_at_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.sm_at_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>
      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_at_ID" placeholder="" :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="addOptions.data.sm_at_Name" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-date-picker
              v-model="addOptions.data.sm_at_CreateTime"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_at_Cost" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_at_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateAdTypeObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeObj.sm_at_ID" placeholder=""  :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="updateAdTypeObj.sm_at_Name" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-date-picker
              v-model="updateAdTypeObj.sm_at_CreateTime"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeObj.sm_at_Cost" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeObj.sm_at_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        isOff:true,
        formLabelWidth:'120px',
        isLoading:false,
        adTypeName:'',
        addDialog:false,
        updateDialog:false,
        total:0,
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_at_ID": "1",//
            "sm_at_Name": "",//
            "sm_at_CreateTime": "",//
            "sm_at_Cost": "",//
            "sm_at_Remark": "",//
          }
        }
      }
    },
    computed: mapGetters([
      'adTypeList',
      'updateAdTypeObj',
      'adTypeNameList'
    ]),
    created(){
      this.initData(this.adTypeName)
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(this.adTypeName,num)
      },
      //
      search(){
        this.initData(this.adTypeName)
      },
      initData(name,page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_at_ID": "",//
          "sm_at_Name": name?name:"",//
          "sm_at_CreateTime": "",//
          "sm_at_Cost": "",//
          "sm_at_Remark": "",//
          "page": page?page:1,
          "rows": 10
        };
        this.isLoading = true;
        this.$store.dispatch('initAdTypeList',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      add(){
        this.addDialog=true;
        this.$store.commit('setTranstionFalse')
      },
      addSubmit(){
        this.$store.dispatch('AddAdType',this.addOptions)
          .then(
            (suc)=>{
              this.$notify(
                {
                  message: suc,
                  type:"success"
                }
              );
              this.initData(this.adTypeName)
            },
            (err)=>{
              this.$notify(
                {
                  message:err,
                  type:'error'
                }
              );
            }
            )
        this.addDialog=false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdTypeObj',id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateAdTypeObj
        };
        this.$store.dispatch('UpdateAdType',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adTypeName)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false
      },
      //
      Delete(id){
        let deleteOptions= {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data":{
            "sm_at_ID": id,//
          }
        }
        this.$store.dispatch('deleteAdType',deleteOptions)
          .then(
            (suc)=>{
              this.$notify(
                {
                  message: suc,
                  type: "success"
                }
              );
              this.initData(this.adTypeName)
            },
            (err)=>{
              this.$notify(
                {
                  message:err,
                  type:"error"
                }
              )
            }
          )
      },
    },
  }
</script>
<style scoped>

</style>



