<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="adPosition" auto-complete="off"  placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table
        :data="adPositionList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="sm_pi_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_pi_LocationNo">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_pi_MaxNo">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_pi_Name">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_pi_Remark">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.sm_pi_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.sm_pi_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_pi_ID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_pi_LocationNo" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_pi_MaxNo" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_pi_Name" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_pi_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addAdPositionSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateAdPositionObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdPositionObj.sm_pi_ID" placeholder=""  :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdPositionObj.sm_pi_LocationNo" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdPositionObj.sm_pi_MaxNo" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdPositionObj.sm_pi_Name" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdPositionObj.sm_pi_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        isOff:true,
        isLoading:false,
        total:0,
        formLabelWidth:'120px',
        adPosition:'',
        addDialog:false,//
        updateDialog:false,//
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_pi_ID": "1",//
            "sm_pi_LocationNo": "1",//
            "sm_pi_MaxNo": "",//
            "sm_pi_Name": "",//
            "sm_pi_Remark": "",//
          }
        },//
      }
    },
    computed: mapGetters([
      'updateAdPositionObj',
      'adPositionList',
    ]),
    created(){
      this.initData(this.adPosition)
    },
    methods: {
      //
      initData(name){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_pi_ID": "",//
          "sm_pi_LocationNo": "",//
          "sm_pi_MaxNo": "",//
          "sm_pi_Name": name?name:'',//
          "sm_pi_Remark": "",//
        }
        this.isLoading = true;
        this.$store.dispatch('initAdPosition',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search(){
        this.initData(this.adPosition)
      },
      //
      Add(){
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addAdPositionSubmit(){
        this.$store.dispatch('AddAdPosition',this.addOptions)
          .then((suc)=>{this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adPosition)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdPositionObj',id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateAdPositionObj
        };
        this.$store.dispatch('UpdateAdPosition',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adPosition)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_pi_ID": id,//
          }
        };
        this.$store.dispatch('DeleteAdPosition',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adPosition)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
  }
</script>
<style scoped>

</style>



