<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="userName" auto-complete="off" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <!--<el-button type="primary" @click="next"></el-button>-->
            <el-button type="primary" @click="addAdminAgentsProfit" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="adminAgentsProfitList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          sortable
          prop="sm_al_ID"
        >
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_cp_Name"
        >
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_al_ProxyLevel"
        >
        </el-table-column>
        <el-table-column
          align="center"
          label=""
        >
          <template slot-scope="scope">
            <span>{{parseInt(scope.row.sm_al_Percent*100)}}%</span>
          </template>
        </el-table-column>
        <el-table-column label=""  align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.sm_al_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.sm_al_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;padding: 10px 0">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.data.sm_cp_ID" placeholder="">
              <el-option
                v-for="item in partnershipTypeList"
                :key="item.sm_cp_ID"
                :label="item.sm_cp_Name"
                :value="item.sm_cp_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-radio-group v-model="addOptions.data.sm_al_ProxyLevel">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
              <el-radio :label="3"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_al_Percent" placeholder="" ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addAdminVendorProfitSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateAdminAgentsProfitObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateAdminAgentsProfitObj.sm_cp_ID" placeholder="">
              <el-option
                v-for="item in partnershipTypeList"
                :key="item.sm_cp_ID"
                :label="item.sm_cp_Name"
                :value="item.sm_cp_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-radio-group v-model="updateAdminAgentsProfitObj.sm_al_ProxyLevel">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
              <el-radio :label="3"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdminAgentsProfitObj.sm_al_Percent" placeholder="" ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateAdminVendorProfitSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        userName:'',
        total:0,
        isLoading:false,
        addDialog:false,//
        updateDialog:false,//
        formLabelWidth:'120px',
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_cp_ID": '',//
            "sm_al_ProxyLevel": '',//(1.,2.,3)
            "sm_al_Percent": '',
          }
        }
      }
    },
    computed: mapGetters([
      'partnershipTypeList',
      'adminAgentsProfitList',
      'updateAdminAgentsProfitObj'
    ]),
    created(){
      this.initData(1)
      //
      let options = {
        "loginUserID": "huileyou",
        "loginUserPass": "123",
        "operateUserID": "",
        "operateUserName": "",
        "pcName": "",
        "sm_cp_ID": "",
        "sm_cp_Name": "",
        "sm_cp_IsDelete": 0
      };
       this.$store.dispatch('initPartnershipType',options)
        .then(()=>{},err=>{
           this.$notify({
             message: err,
             type: 'error'
           });
         })
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(num)
      },
      initData(page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_al_ID": "",//
          "sm_cp_ID": "",//
          "sm_al_ProxyLevel": "",//(1.,2.,3)
          "page": page?page:1,
          "rows": 5,
        }
        this.isLoading = true;
        this.$store.dispatch('initAdminAgentsProfit',options)
        .then(total=>{
          this.total = total;
          this.isLoading = false;
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      search(){
        this.initData(1)
      },
      //
      addAdminAgentsProfit(){
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addAdminVendorProfitSubmit(){
        if(Number(this.addOptions.data.sm_al_Percent)>=1||Number(this.addOptions.data.sm_al_Percent)<0||isNaN(this.addOptions.data.sm_al_Percent)){
          this.$notify({
            message: '0~1',
            type: 'error'
          });
          return
        }
        this.$store.dispatch('AddAdminVendorProfit',this.addOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(1)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
        this.addDialog = false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdminAgentsProfitObj',id)
      },
      //
      updateAdminVendorProfitSubmit(){
        if(Number(this.updateAdminAgentsProfitObj.sm_al_Percent)>=1||Number(this.updateAdminAgentsProfitObj.sm_al_Percent)<0||isNaN(this.updateAdminAgentsProfitObj.sm_al_Percent)){
          this.$notify({
            message: '0~1',
            type: 'error'
          });
          return
        }
        delete this.updateAdminAgentsProfitObj.sm_cp_Name
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateAdminAgentsProfitObj
        }
        this.$store.dispatch('UpdateAdminVendorProfit',options)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(1)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ID": id////
        }
        this.$store.dispatch('DeleteAdminVendorProfit',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
  }
</script>
<style scoped>

</style>



