<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="scenicSpotId" placeholder="" size="small">
              <el-option
                v-for="item in showTopList"
                :key="item.tm_ts_Code"
                :label="item.tm_ts_Name"
                :value="item.tm_ts_Code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>


      <!---->

      <el-table
        :data="ticketHomePageBigPictureList"
        stripe
        style="width: 100%">
        <el-table-column
          align="center"
          prop="tm_ts_Name"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          label="">
          <template slot-scope="scope">
            <img  width="89" height="29" @click="bigPicture(scope.row.tm_tbi_Image)" v-lazy="scope.row.tm_tbi_Image">
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="tm_tbi_Remark"
          label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="success" size="mini" @click="update(scope.row)">
              
            </el-button>
            <el-button type="danger" size="mini" @click="Delete(scope.row.tm_tbi_ID)">
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <el-dialog title="" :visible.sync="bigImageDialog" width="50%">
        <img v-lazy="bigImage" width="100%" height="100%">
        <span slot="footer" class="dialog-footer">
          <el-button @click="bigImageDialog = false"></el-button>
        </span>
      </el-dialog>


      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.tm_ts_Code" placeholder="" size="small">
              <el-option
                v-for="item in showTopList"
                :key="item.tm_ts_Code"
                :label="item.tm_ts_Name"
                :value="item.tm_ts_Code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*">
            </a>
            <img v-lazy="addOptions.tm_tbi_Image" width="192" height="120" v-show="addOptions.tm_tbi_Image">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tbi_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateData">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateData.tm_ts_Code" placeholder="" size="small">
              <el-option
                v-for="item in showTopList"
                :key="item.tm_ts_Code"
                :label="item.tm_ts_Name"
                :value="item.tm_ts_Code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*">
            </a>
            <p></p>
            <img v-lazy="addOptions.tm_tbi_Image" width="192" height="120" v-show="addOptions.tm_tbi_Image">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateData.tm_tbi_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'ticketHomePageBigPictureList',
      'showTopList'
    ]),
    data() {
      return {
        bigImageDialog: false,
        bigImage: '',
        scenicSpotId: '',
        addDialog: false,
        formLabelWidth: '120px',
        addOptions: {
          "tm_ts_Code": "",//
          "tm_tbi_Image": "",//
          "tm_tbi_Remark": "",//
        },
        updateDialog: false,
        updateData: {},
      }
    },
    methods: {
      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },
      uploaNode() {
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('uploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.addOptions.tm_tbi_Image = data.data;
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
        }, 30)
      },
      //
      initTicketList() {
        let getTourSite = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": "",//()
          "tm_ts_Name": "",//
          "tm_ts_TradeInfoID": "",//
          "tm_ts_ThemeTypeID": "",//
          "tm_ts_IsHot": "",//01
          "tm_ts_ShowTop": "",//01
          "tm_ts_ShowTopIsAgree": "",//(012)
          "tm_ts_IsPass": "",//(012)
          "tm_ts_IsDelete": 0,//(01)
          "page": 1,
          "rows": 10000
        };
        this.$store.dispatch('initScenicSpotList', getTourSite)
          .then(() => {
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      //
      initData(id) {
        let selectTopBigImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_tbi_ID": "",//
          "tm_ts_Code": id ? id : "",//
          "tm_tbi_IsDelete": "0"
        };
        this.$store.dispatch('initTicketHomePageBigPicture', selectTopBigImageInfo)
          .then(() => {
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.scenicSpotId);
      },
      //
      bigPicture(url) {
        this.$store.commit('setTranstionFalse');
        this.bigImageDialog = true;
        this.bigImage = url;
      },
      //
      add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        this.uploaNode();
      },
      //
      addSubmit() {
        let insertTopBigImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addTicketHomePageBigPicture', insertTopBigImageInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      update(rowData) {
        this.addOptions.tm_tbi_Image = '';
        this.uploaNode();
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.updateData = rowData;
      },
      //
      updateSubmit() {
        if (this.addOptions.tm_tbi_Image) {
          this.updateData.tm_tbi_Image = this.addOptions.tm_tbi_Image
        }
        let updateTopBigImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateData
        };
        this.$store.dispatch('updateTicketHomePageBigPicture', updateTopBigImageInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateDialog = false;
      },
      //
      Delete(id) {
        let deleteTopBigImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "tm_tbi_ID": id ? id : '',//
          }
        };
        this.$store.dispatch('deleteTicketHomePageBigPicture', deleteTopBigImageInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
    },
    created() {
      this.initTicketList();
      this.initData();
    }
  }
</script>
<style scoped>
</style>



