<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>

      <!---->
      <el-table
        :data="showTopList"
        v-loading="isLoading"
        style="width: 100%; margin-top: 20px;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="():">
                <span>{{ props.row.tm_ts_Code }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ThemeTypeID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_bk_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_tm_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_GreatName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CountrieName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ProviceName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CityName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ContryName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <img v-for="item,index in props.row.tm_ts_ShowImage" :src="item" width="300" height="150"
                     style="margin-top: 10px;">
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Introduce }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Detailedintroduction }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Address }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Opentime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Time }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Phone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Longitude }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Latitude }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsHot }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsOversea }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsSeasonChoice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_SuggestPrice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CreateTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Remark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Phone">
        </el-table-column>
        <el-table-column label="" align="center" style="width: 1000px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="toExamineS(scope.row.tm_ts_Code)"
            >
              <template>
                {{scope.row.showTopIsAgreeLabel}}
              </template>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <el-dialog title="" :visible.sync="toExamineDialog">
        <el-form :model="toExamine">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="toExamine.tm_ts_ShowTopIsAgree" placeholder="">
              <el-option
                v-for="item in isPass"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="toExamineDialog = false"> </el-button>
          <el-button type="primary" @click="toExamineSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total">
        </el-pagination>
      </div>

    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'showTopList'
    ]),
    data() {
      return {
        isLoading: false,
        isPass: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          }
        ],
        toExamineDialog: false,
        formLabelWidth: '120px',
        toExamine: {
          "tm_ts_Code": "",
          "tm_ts_ShowTopIsAgree": "",
        },
        total:0,
      }
    },
    methods: {
//      
      handleCurrentChange(num) {
        this.initData(num)
      },
      //
      initData(num) {
        let getTourSite = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tsCode": "",
          "tsName": "",
          "tradeInfoID": "",
          "isDelete": 0,
          "page": num ? num : 1,
          "rows": 5
        };
        this.$store.dispatch('initShowTop', getTourSite)
          .then(total=>{
            this.total = total;
          })
      },
      //
      toExamineS(id) {
        this.$store.commit('setTranstionFalse');
        this.toExamine.tm_ts_Code = id;
        this.toExamineDialog = true;
      },
      //
      toExamineSubmit() {
        let isPassValidate = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": this.toExamine.tm_ts_Code,
          "tm_ts_ShowTopIsAgree": this.toExamine.tm_ts_ShowTopIsAgree,
        };
        this.$store.dispatch('toExamineShowHomePageSubmit',isPassValidate)
          .then(suc=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData()
          },err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.toExamineDialog = false;
      },
    },
    created() {
      this.initData();
    },
  }
</script>
<style scoped>
</style>



