<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="userName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="statusValue" placeholder="" size="small">
              <el-option
                v-for="item in ApprovalStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <!--<el-button type="primary" @click="next"></el-button>-->
            <!--<el-button type="primary" @click="addAdminBusinessInformation"></el-button>-->
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="adminSupplierList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_AgentID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_Telephone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_Sex | getSex }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_FixPhoneAreaCode+"-" +props.row.agentInfo.sm_ai_FixPhoneContryCode}}</span>
              </el-form-item>
              <el-form-item label="QQ:">
                <span>{{ props.row.agentInfo.sm_ai_QQ }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_FaxNo}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_Email}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_GoodName}}</span>
              </el-form-item>
              <el-form-item label=":">
               <span v-for="item in props.row.agentInfoScopeList" style="margin-right: 10px">{{item.sm_ts_Name}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_Address}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_RegTime}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_RegMoney}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_CompanyPersons}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_BalanceCurrencyName}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span v-for="item in props.row.agentInfoTypeList" style="margin-right: 10px">{{item.sm_cp_PartnerTypeName}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_GoodIntroduce}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_PartnerWay}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_IncludeCert | getIncludeCert}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.agentInfo.sm_ai_CertNo}}</span>
              </el-form-item>
              <el-form-item label=" :">
                <span>{{ props.row.agentInfo.sm_ai_CertExpireTo}}</span>
              </el-form-item>
              <el-form-item label=" :">
                <img :src="item" alt="" v-for="item in props.row.agentInfo.sm_ai_CertImage"
                     style="width: 100px;height: 100px" v-show="props.row.agentInfo.sm_ai_CertImage.length">
              </el-form-item>
              <el-form-item label=" :">
                <span>{{ props.row.agentInfo.sm_ai_FeeNo}}</span>
              </el-form-item>
              <el-form-item label=" :">
                <img :src="item" alt="" v-for="item in props.row.agentInfo.sm_ai_FeeImage"
                     style="width: 100px;height: 100px" v-show="props.row.agentInfo.sm_ai_FeeImage.length">
              </el-form-item>
              <el-form-item label=" :">
                <span>{{ props.row.agentInfo.sm_ai_OtherCert}}</span>
              </el-form-item>
              <el-form-item label=" :">
                <img :src="item" alt="" v-for="item in props.row.agentInfo.sm_ai_OtherImage"
                     style="width: 100px;height: 100px" v-show="props.row.agentInfo.sm_ai_OtherImage.length">
              </el-form-item>
              <el-form-item label=" :">
                <span>{{ props.row.agentInfo.sm_ai_IsPass | getPass}}</span>
              </el-form-item>
              <el-form-item label=" :">
                <span>{{ props.row.agentInfo.sm_ai_CreateTime}}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="agentInfo.sm_ai_ID">
        </el-table-column>
        <el-table-column
          label=""
          prop="agentInfo.sm_ai_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="agentInfo.sm_ai_Telephone">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              v-show="scope.row.agentInfo.sm_ai_IsPass!=1"
              @click="approval(scope.row.agentInfo.sm_ai_ID)">
            </el-button>
            <!--<el-button-->
              <!--size="mini"-->
              <!--type="danger"-->
              <!--@click="deleteAdminSupplier(scope.row.agentInfo.sm_ai_ID)">-->
            <!--</el-button>-->
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>
    </section>

    <el-dialog title="" :visible.sync="approvalStatusDialog">
      <el-form :model="approvalOptions">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-radio v-model="approvalOptions.IsPass" label="1"></el-radio>
          <el-radio v-model="approvalOptions.IsPass" label="2"></el-radio>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth" v-show="approvalOptions.IsPass!=1">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 6}"
            placeholder=""
            v-model="approvalOptions.failReason">
          </el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth" v-show="approvalOptions.IsPass!=2">
          <el-input v-model="approvalOptions.payPercent" placeholder="" type="number"></el-input>
        </el-form-item>
        <!--<el-form-item label=":" :label-width="formLabelWidth" v-show="approvalOptions.IsPass!=2">-->
          <!--<el-input v-model="approvalOptions.agentPassword" placeholder=""></el-input>-->
        <!--</el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="approvalStatusDialog = false"> </el-button>
        <el-button type="primary" @click="approvalStatusSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        approvalStatusDialog:false,//
        statusValue:'',
        total:0,
        userName:'',
        isLoading:false,
        status:1,//
        sm_ai_ID:'',//
        formLabelWidth:'120px',
        approvalOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_ai_ID": '',    //
          "IsPass": '1',
          payPercent:'',
          failReason:'',//
          "agentCode": "",//
          "agentPassword":"",//
          "reviewPerson":"huileyou"
        },
        ApprovalStatus:[
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          },
        ],//
      }
    },
    computed: mapGetters([
      'adminSupplierList'
    ]),
    created(){
      this.initData('',1)
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData('',num)
      },
      initData(name,page,status){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          sm_ai_Name:name?name:'',
          "sm_ai_ID": '',
          "page": page?page:1,
          "rows": 5,
        };
        this.isLoading = true;
        this.$store.dispatch('initAdminSupplier',options)
        .then(data=>{
          this.total = Number(data.totalrows);
          this.isLoading = false;
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      search(){
        this.initData(this.userName.trim())
      },
      //
      approval(id){
        this.$store.commit('setTranstionFalse');
        this.approvalStatusDialog = true;
        this.sm_ai_ID = id;
      },
      //
      approvalStatusSubmit(){
        if(Number(this.approvalOptions.payPercent)>=1){
          this.$notify({
            message: '1',
            type: 'error'
          });
          return
        }
        if(this.approvalOptions.IsPass==1){
          this.approvalOptions.failReason = ''
        }
        if(this.approvalOptions.IsPass==2){
          this.approvalOptions.payPercent = ''
        }

        this.approvalOptions.sm_ai_ID = this.sm_ai_ID
        this.$store.dispatch('GetApproval',this.approvalOptions)
        .then(success=>{
          this.$notify({
            message: success,
            type: 'success'
          });
          this.initData(this.userName.trim())
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
        this.approvalStatusDialog = false;
      }
    },
  }
</script>
<style scoped>

</style>



