<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="userName" auto-complete="off"size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table
        :data="adChargeWayList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="sm_icw_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_icw_Name">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_icw_Remark">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.sm_icw_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.sm_icw_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="addOptions.data.sm_icw_Name" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_icw_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateAdChargeWayObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdChargeWayObj.sm_icw_ID" placeholder=""  :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="updateAdChargeWayObj.sm_icw_Name" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdChargeWayObj.sm_icw_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        isLoading:false,
        total:0,
        formLabelWidth:'120px',
        userName:'',
        isOff:true,
        addDialog:false,//
        updateDialog:false,//
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_icw_ID": "1",//
            "sm_icw_Name": "",//
            "sm_icw_Remark": "",//
          }
        },//
      }
    },
    computed: mapGetters([
      'adChargeWayList',
      'updateAdChargeWayObj'
    ]),
    created(){
      this.initContent().then(()=>{},err=>{
        this.$notify({
          message: err,
          type: 'error'
        });
      })
    },
    methods: {
      async initContent(){
        //
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_icw_ID": "",//
          "sm_icw_Name": "",//
          "sm_icw_Remark": "",//
        };
        await this.$store.dispatch('initAdChargeWayList',options)
      },
      //
      handleCurrentChange(num){
        this.initData(num)
      },
      //
      initData(name){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_icw_ID": "",//
          "sm_icw_Name": name?name:"",//
          "sm_icw_Remark": "",//
        }
        this.isLoading = true;
        this.$store.dispatch('initAdChargeWayList',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search(){
        this.initData(this.userName)
      },
      add(){
        this.addDialog=true;
        this.$store.commit('setTranstionFalse')
      },
      addSubmit(){
        this.$store.dispatch('addAdChargeWay',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message:suc,
              type:"success"
            });
            this.initData(this.userName)
          },(err)=>{
            this.$notify({
              message:err,
              type:"error"
            })
          })
        this.addDialog=false;
      },
      Update(id){
        this.updateDialog=true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdChargeWayObj',id)
      },
      updateSubmit(){
        let updateOptions={
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateAdChargeWayObj
        }
        // updateOptions.data.sm_icw_ID = updateOptions.data.sm_icw_ID+''
        this.$store.dispatch('UpdateAdChargeWay',updateOptions)
          .then((suc)=>{
            this.$notify({
              message:suc,
              type:"success"
            });
            this.initData(this.userName)
          },(err)=>{
            this.$notify({
              message:err,
              type:"error"
            })
          })
        this.updateDialog=false;
      },
      Delete(id){
        let deleteOptions={
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_icw_ID": id,//
          }

        };
        this.$store.dispatch('deleteAdChargeWay',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message:suc,
              type:"success"
            });
            this.initData(this.userName)
          },(err)=>{
            this.notify({
              message:err,
              type:"error"
            })
          })
      }
    },
  }
</script>
<style scoped>

</style>



