<template>
  <div>
    <!---->
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="imageName" auto-complete="off" placeholder="" size="small"></el-input>
            <el-select v-model="addOptions.data.fd_is_ID" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="foodRecommendStoreTypeList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="fd_is_ID">
        </el-table-column>
         <el-table-column
          align="center"
          label=""
          prop="fd_sf_ProductName">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="fd_sf_Introduce">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="fd_pi_ImageUrl"
        >
          <template slot-scope="scope">
            <img height="50" width="100" v-lazy="scope.row.fd_pi_ImageUrl" alt="">
          </template>
        </el-table-column>

        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.fd_is_ID)">
            </el-button>
            <el-button
              v-show="true"
              size="mini"
              type="danger"
              @click="Delete(scope.row.fd_is_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
    <!--      <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.data.fd_is_ID" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>-->
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.fd_is_ShopID" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateFoodRecommendStoreTypeObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="fd_py_ParentID" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateFoodRecommendStoreTypeObj.propertyName" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    data(){
      return {

        fd_py_ParentID:'',
        options:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'28'
          },
        ],
        addDialog:false,
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_is_ShopID": "",//
          }
        },
        //
        imageName:'',
        total:0,
        isLoading:false,
        updateDialog:false,
        formLabelWidth:'120px'
      }
    },
    computed: mapGetters([
      'foodRecommendStoreTypeList',
      'updateFoodRecommendStoreTypeObj',

    ]),
    created(){
      this.initData('',1)
    },
    methods: {
      //

      handleCurrentChange(num){
        this.initData(this.imageName,num)
      },

      initData(name){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          //"fd_is_ID": "2",//
          "fd_is_ShopID": name?name:"",//
        };
        this.isLoading = true;
        this.$store.dispatch('initIntroduceShopInfo',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search(){
        this.initData(this.imageName,1)
      },


      //
      Add(){
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addSubmit(){
        //actions
        this.$store.dispatch('addFoodRecommendStoreType',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.imageName,1) //
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.addDialog = false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateFoodAttributeType',id)   //initUpdataFoodRecommendStoreTypemutations
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",

//         data: this.updateFoodAttributeTypeObj  //stategetters
          "data": {

            "fd_py_ID": this.updateFoodAttributeTypeObj.propertyID,//
            "fd_py_Name": this.updateFoodAttributeTypeObj.propertyName,//
            "fd_py_ParentID": this.fd_py_ParentID,//

          }
        };
        updateOptions.data.letterName = ''
        this.$store.dispatch('initUpdateFoodAttributeType',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.imageName,1)//**************/
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_py_ID": id
          }
        };
//        DeleteFoodAttributeType
        this.$store.dispatch('DeleteFoodAttributeType',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.imageName,1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
      }
    }
  }
</script>
<style scoped>

</style>



