<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="adTypeChargeMode" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="adTypeChargeModeList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="sm_aicw_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_at_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_icw_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_icw_Name">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="sm_aicw_Remark">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.sm_aicw_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.sm_aicw_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aicw_ID" placeholder="" :disabled="isOff" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_at_ID" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_icw_ID" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aicw_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateAdTypeChargeModeObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeChargeModeObj.sm_aicw_ID" placeholder=""  :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeChargeModeObj.sm_icw_ID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeChargeModeObj.sm_at_ID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdTypeChargeModeObj.sm_aicw_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        isOff:true,
        formLabelWidth:'120px',
        isLoading:false,
        adTypeChargeMode:'',
        addDialog:false,
        updateDialog:false,
        total:0,
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "page": "1",
            "rows": "4",
            "sm_aicw_ID": "1",//
            "sm_icw_ID": "",//
            "sm_at_ID": "",//
            "sm_aicw_Remark": "",//
          }
        },
      }
    },
    computed: mapGetters([
      'adTypeChargeModeList',
      'updateAdTypeChargeModeObj'
    ]),
    created(){
      this.initData(this.adTypeChargeMode)
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(this.adTypeChargeMode,num)
      },
      //
      search(){
        this.initData(this.adTypeChargeMode)
      },
      initData(name,page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_aicw_ID": name?name:"",//
          "sm_icw_ID": "",//
          "sm_at_ID": '',//
          "sm_aicw_Remark": "",//
          "page": page?page:1,
          "rows": 4
        };
        this.isLoading = true;
        this.$store.dispatch('initAdTypeChargeMode',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      Add(){
        let obj = this.addOptions.data;
        obj.sm_icw_ID ='';
        obj.sm_at_ID ='';
        obj.sm_aicw_Remark ='';
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addSubmit(){
        this.$store.dispatch('AddAdTypeChargeMode',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adTypeChargeMode)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.addDialog = false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdTypeChargeMode',id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          // "data": this.updateAdTypeChargeModeObj
          "data": {
            "sm_aicw_ID":this.updateAdTypeChargeModeObj.sm_aicw_ID,
            "sm_aicw_Remark":this.updateAdTypeChargeModeObj.sm_aicw_Remark,
            "sm_at_ID":this.updateAdTypeChargeModeObj.sm_at_ID,
            "sm_icw_ID":this.updateAdTypeChargeModeObj.sm_icw_ID,
          }

        };
        this.$store.dispatch('UpdateAdTypeChargeMode',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adTypeChargeMode)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_aicw_ID": id,//
          }
        };
        this.$store.dispatch('DeleteAdTypeChargeMode',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adTypeChargeMode)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
      }
    },
  }
</script>
<style scoped>

</style>



