<template>
  <div>

    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="userName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <!--<el-form-item>-->
            <!--<el-select v-model="statusValue" placeholder="">-->
              <!--<el-option-->
                <!--v-for="item in ApprovalStatus"-->
                <!--:key="item.value"-->
                <!--:label="item.label"-->
                <!--:value="item.value">-->
              <!--</el-option>-->
            <!--</el-select>-->
          <!--</el-form-item>-->
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <!--<el-button type="primary" @click="next"></el-button>-->
            <!--<el-button type="primary" @click="addAdminBusinessInformation"></el-button>-->
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="adminAgentsList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_ProxyInfoID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_CertNo }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_Telephone }}</span>
              </el-form-item>
              <el-form-item label="QQ:">
                <span>{{ props.row.sm_pi_QQ }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_Email }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_Sex | getSex }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_Address }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_RegTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_RegMoney }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_CertNo }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.sm_pi_CertExpireTo }}</span>
              </el-form-item>
              <el-form-item label=" :">
                <img :src="item" alt="" v-for="item in props.row.sm_pi_CertImage"
                     style="width: 100px;height: 100px" v-show="props.row.sm_pi_CertImage.length">
              </el-form-item>
              <el-form-item label=" :">
                <span>{{ props.row.sm_pi_FeeNo}}</span>
              </el-form-item>
              <el-form-item label=" :">
                <img :src="item" alt="" v-for="item in props.row.sm_pi_FeeImage"
                     style="width: 100px;height: 100px" v-show="props.row.sm_pi_FeeImage.length">
              </el-form-item>


            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="sm_pi_ProxyInfoID">
        </el-table-column>
        <el-table-column
          label=""
          prop="sm_pi_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="sm_pi_Telephone">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              v-show="scope.row.sm_pi_IsPass!=1"
              @click="agents(scope.row.sm_pi_ProxyInfoID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>
    </section>

    <el-dialog title="" :visible.sync="approvalStatusDialog">
      <el-form :model="options">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-radio v-model="options.isPass" label="1"></el-radio>
          <el-radio v-model="options.isPass" label="2"></el-radio>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth" v-show="options.isPass!=1">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 6}"
            placeholder=""
            v-model="options.failReason">
          </el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth" v-show="options.isPass!=2">
          <el-input v-model="options.getPercent" placeholder="" type="number"></el-input>
        </el-form-item>
        <!--<el-form-item label=":" :label-width="formLabelWidth" v-show="approvalOptions.IsPass!=2">-->
        <!--<el-input v-model="approvalOptions.agentPassword" placeholder=""></el-input>-->
        <!--</el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="approvalStatusDialog = false"> </el-button>
        <el-button type="primary" @click="agentsStatusSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {

        formLabelWidth:'120px',
        userName:'',
        isLoading:false,
        total:0,
        approvalStatusDialog:false,
        proxyID:'',
        options :{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "proxyID": "",    //
          "isPass": '1',   //1  2
          "failReason": "",//
          "getPercent": '',//  --     
          'reviewPerson':'huileyou'
        }
      }
    },
    computed: mapGetters([
      'adminAgentsList'
    ]),
    created(){
      this.initData('',1)
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      //
      agents(id){
        this.$store.commit('setTranstionFalse');
        this.approvalStatusDialog = true;
        this.proxyID = id;
      },
      //
      agentsStatusSubmit(){
        if(Number(this.options.getPercent)>=1){
          this.$notify({
            message: '1',
            type: 'error'
          });
          return
        }
        if(this.options.isPass==1){
          this.options.failReason = ''
        }
        if(this.options.isPass==2){
          this.options.getPercent = ''
        }
        this.options.proxyID = this.proxyID
        this.$store.dispatch('checkProxyInfo',this.options)
        .then(success=>{
          this.$notify({
            message: success,
            type: 'success'
          });
          this.initData(this.userName.trim())
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
        this.approvalStatusDialog = false;
      },
      //
      handleCurrentChange(num){
        this.initData('',num)
      },
      //
      search(){
        this.initData(this.userName.trim())
      },
      initData(name,page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "proxyID": "",
          proxyName:name?name:'',
          "page": page?page:1,
          "row":5,
        };
        this.isLoading = true
        this.$store.dispatch('initAdminAgents',options)
        .then(data=>{
          this.total = Number(data.totalrows);
          this.isLoading = false;
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      }
    },
  }
</script>
<style scoped>

</style>



