<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <!---->
      <!--<el-col :span="24" class="formSearch">-->
        <!--<el-form :inline="true">-->
          <!--<el-form-item>-->
            <!--<span>:</span>-->
          <!--</el-form-item>-->
          <!--<el-form-item>-->
            <!--<el-input type="text" v-model="toExamineID" auto-complete="off" placeholder="" size="small"></el-input>-->
          <!--</el-form-item>-->
          <!--<el-form-item>-->
            <!--<el-button type="primary" @click="search" size="small"></el-button>-->
          <!--</el-form-item>-->
        <!--</el-form>-->
      <!--</el-col>-->
      <!---->
      <el-table
        :data="scenicSpotList"
        v-loading="isLoading"
        style="width: 100%; margin-top: 20px;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="():">
                <span>{{ props.row.tm_ts_Code }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ThemeTypeID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_bk_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_tm_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_GreatName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CountrieName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ProviceName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CityName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ContryName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <img v-for="item,index in props.row.tm_ts_ShowImage" :src="item" width="300" height="150"
                     style="margin-top: 10px;">
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Introduce }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Detailedintroduction }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Address }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Opentime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Time }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Phone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Longitude }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Latitude }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsHot }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsOversea }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsSeasonChoice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_SuggestPrice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CreateTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Remark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Phone">
        </el-table-column>
        <el-table-column label="" align="center" style="width: 1000px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="look(scope.row.tm_ts_Code)">
            </el-button>
            <el-button
              size="mini"
              @click="toExamineS(scope.row.tm_ts_Code)"
              v-show="scope.row.tm_ts_IsPass == 0"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <el-dialog title="" :visible.sync="toExamineDialog">
        <el-form :model="toExamine">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="toExamine.tm_ts_IsPass" placeholder="" @change="showReason">
              <el-option
                v-for="item in isPass"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth" v-show="isShow">
            <el-input v-model="toExamine.tm_ts_FailReason"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="toExamineDialog = false"> </el-button>
          <el-button type="primary" @click="toExamineSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total">
        </el-pagination>
      </div>

    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'scenicSpotList'
    ]),
    data() {
      return {
        toExamineID:'',
        isLoading: false,
        isPass: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          }
        ],
        toExamineDialog: false,
        formLabelWidth: '120px',
        toExamine: {
          "tm_ts_Code": "",
          "tm_ts_IsPass": "",
          "tm_ts_FailReason": ""
        },
        isShow: false,
        total: 0,
      }
    },
    methods: {
//      
      handleCurrentChange(num) {
        this.initData(num)
      },
      //
      initData(num) {
        let getTourSite = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": "",    //
          "tm_ts_Name": "",//
          "tm_ts_TradeInfoID": "",//
          "tm_ts_IsDelete": 0,//
          "tm_ts_IsPass": "",//(012)
          "tm_ts_ShowTop": "",//01
          "tm_ts_IsHot": "",//01)
          "tm_ts_ThemeTypeID": "",//
          "page": num ? num : 1,
          "rows": 5
        };
        this.$store.dispatch('initScenicSpot', getTourSite)
          .then(total => {
            this.total = total
          })
      },
//      
      search(){
        this.initData()
      },
      //
      look(id) {
        let selectIsPass = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": id
        };
        this.$store.dispatch('ticketLookToExamine', selectIsPass)
          .then(information => {
            this.$alert(information, {
              confirmButtonText: ''
            });
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      toExamineS(id) {
        this.$store.commit('setTranstionFalse');
        this.toExamine.tm_ts_Code = id;
        this.toExamineDialog = true;
      },
      showReason() {
        if (this.toExamine.tm_ts_IsPass == 2) {
          this.isShow = true;
        } else {
          this.isShow = false;
        }
      },
      //
      toExamineSubmit() {
        let isPassValidate = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": this.toExamine.tm_ts_Code,
          "tm_ts_IsPass": this.toExamine.tm_ts_IsPass,
          "tm_ts_FailReason": this.toExamine.tm_ts_FailReason,
        };
        this.$store.dispatch('toExamineSubmit', isPassValidate)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData()
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.toExamineDialog = false;
      },
    },
    created() {
      this.initData();
    },
  }
</script>
<style scoped>
</style>



