<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="themeName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="ticketThemeList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="tm_tt_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="tm_tt_Name">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="tm_tt_Remark">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.tm_tt_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.tm_tt_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.tm_tt_Name" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.tm_tt_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateTicketThemeObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketThemeObj.tm_tt_ID" placeholder=""  :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketThemeObj.tm_tt_Name" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketThemeObj.tm_tt_Remark" placeholder="" type="textarea" :rows="4"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        isOff:true,
        formLabelWidth:'120px',
        isLoading:false,
        themeName:'',
        addDialog:false,
        updateDialog:false,
        total:0,
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "tm_tt_Name": "",
            "tm_tt_Remark": ""
          }
        }
      }
    },
    computed: mapGetters([
      'ticketThemeList',
      'updateTicketThemeObj'
    ]),
    created(){

    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(this.themeName,num)
      },
      //
      search(){
        this.initData(this.themeName)
      },
      initData(name,page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ttID": "",
          "ttName": name?name:'',
          "isDelete": 0,
          "page": page?page:1,
          "rows": 5
        };
        this.isLoading = true;
        this.$store.dispatch('initTicketTheme',options)
        .then(total=>{
          this.total = total;
          this.isLoading = false;
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      Add(){
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addSubmit(){
        this.$store.dispatch('AddTicketTheme',this.addOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.themeName)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
        this.addDialog = false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateTicketTheme',id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateTicketThemeObj
        };
        this.$store.dispatch('UpdateTicketTheme',updateOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.themeName)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ttID": id
        };
        this.$store.dispatch('DeleteTicketTheme',deleteOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.themeName)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
      }
    },
  }
</script>
<style scoped>

</style>



