<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="iconName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="hotelIconGalleryList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          prop="ht_ie_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="ht_ie_Name">
        </el-table-column>
        <!--<el-table-column-->
          <!--align="center"-->
          <!--label=""-->
          <!--prop="ht_ie_Image">-->
        <!--</el-table-column>-->
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.ht_ie_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.ht_ie_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.ht_ie_Name" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateHotelIconGalleryObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateHotelIconGalleryObj.ht_ie_Name" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        iconName:'',
        ImageURL:[],
        ImageURL1:[],
        total:0,
        isLoading:false,
        addDialog:false,
        updateDialog:false,
        formLabelWidth:'120px',
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "ht_ie_Name": "",//
            "ht_ie_Image": "",//
            "ht_id_Remark": "",//
          }
        }
      }
    },
    computed: mapGetters([
      'hotelIconGalleryList',
      'updateHotelIconGalleryObj'
    ]),
    created(){
      this.initData('',1)
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(this.iconName,num)
      },
      //
      search(){
        this.initData(this.iconName)
      },
      //
      initData(name,page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ht_ie_ID": "",//ID
          "ht_ie_Name": name?name:'',//
          "ht_ie_Image": '',//
          "ht_id_Remark": "",//
          page:page?page:1,
          rows:'5'
        }
        this.isLoading = true;
        this.$store.dispatch('initHotelIconGallery',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      Add(){
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      addSubmit(){
        this.$store.dispatch('AddHotelIconGallery',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.iconName,1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.addDialog = false;
      },
      Update(id){
        this.$store.commit('initUpdateHotelIconGallery',id);
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');

      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateHotelIconGalleryObj
        };
        this.$store.dispatch('UpdateHotelIconGallery',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.iconName,1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "ht_ie_ID": id//ID
          }
        };
        this.$store.dispatch('DeleteHotelIconGallery',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.iconName,1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
      }
    },
  }
</script>
<style scoped>

</style>



